import React from "react";
import { Card, Col, Form } from "react-bootstrap";

function ProductSearch({ value, onSearch, onSearchSubmit, ...rest }) {
  return (
    <Card className="mb-3">
      <Card.Body>
        <h5 className="d-inline-block border-bottom mb-2 pb-2 pr-3">Tm kim sn phm</h5>
        <Form onSubmit={onSearchSubmit} className="py-2">
          <Form.Row className="align-items-end">
            <Form.Group className="mb-0" as={Col} sm="3" controlId="keySearch">
              <Form.Control value={value} onChange={onSearch} placeholder="Nhp t kho tm kim..." style={{ width: "100%" }} />
            </Form.Group>
            <Form.Group className="mb-0" as={Col}>
              <button className="btn btn-custom">Tm kim</button>
            </Form.Group>
          </Form.Row>
        </Form>
      </Card.Body>
    </Card>
  );
}

export default ProductSearch;
