import { Descriptions, Divider, Modal } from "antd";
import React from "react";

function OrderDetail({ dataOrder, isModalVisible, handleAccept, handleCancel }) {
  return (
    <Modal
      width="70%"
      style={{ top: 10 }}
      title={<h4 className="m-0">Chi tit n hng</h4>}
      visible={isModalVisible}
      onCancel={handleCancel}
      footer={false}
    >
      <Descriptions title="Thng tin khch hng">
        <Descriptions.Item labelStyle={{ fontWeight: 600 }} label="Tn khch hng">
          {dataOrder.name}
        </Descriptions.Item>
        <Descriptions.Item labelStyle={{ fontWeight: 600 }} label="Email">
          {dataOrder.email}
        </Descriptions.Item>
        <Descriptions.Item labelStyle={{ fontWeight: 600 }} label="S in thoi">
          {dataOrder.phone}
        </Descriptions.Item>
      </Descriptions>
      <br />
      <Divider orientation="center">
        <strong>SN PHM  MUA</strong>
      </Divider>
      {dataOrder?.orderDetail?.map((item, index) => (
        <Descriptions key={index}>
          <Descriptions.Item labelStyle={{ fontWeight: 600 }} label="Sn phm">
            {item.product.name}
          </Descriptions.Item>
          <Descriptions.Item labelStyle={{ fontWeight: 600 }} label="S lng">
            {item.amount}
          </Descriptions.Item>
          <Descriptions.Item labelStyle={{ fontWeight: 600 }} label="n gi">
            {new Intl.NumberFormat("vi-VN", {
              style: "currency",
              currency: "VND",
            }).format(item.price)}
          </Descriptions.Item>
        </Descriptions>
      ))}
      <hr />
      <Descriptions>
        <Descriptions.Item labelStyle={{ fontWeight: 600 }} />
        <Descriptions.Item labelStyle={{ fontWeight: 600 }} label="Tng s lng"></Descriptions.Item>
        <Descriptions.Item labelStyle={{ fontWeight: 600 }} label="Tng tin">
          {new Intl.NumberFormat("vi-VN", {
            style: "currency",
            currency: "VND",
          }).format(dataOrder.total)}
        </Descriptions.Item>
      </Descriptions>
    </Modal>
  );
}

export default OrderDetail;
